@charset "UTF-8";
.main {
  display: flex;
  flex-wrap: wrap;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hang {
  width: 20em;
  height: 15em;
  margin: 0 auto;
}

.gallows {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.gallows__goriz {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 17em;
  height: 0.4em;
  background-color: black;
}

.gallows__goriz-two {
  position: absolute;
  top: 1.5em;
  left: 40%;
  transform: translateX(-50%);
  width: 5.5em;
  height: 0.4em;
  background-color: black;
}

.gallows__vert {
  position: absolute;
  bottom: 0;
  left: 25%;
  width: 0.6em;
  height: 13.5em;
  background-color: black;
}

.gallows__vert-two {
  position: absolute;
  top: 1.5em;
  left: 50%;
  width: 0.2em;
  height: 3.7em;
  background-color: black;
}

.character {
  display: flex;
  justify-content: center;
}

.character__head {
  position: absolute;
  top: 5em;
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  border: 0.3rem solid black;
}

.character__body {
  position: absolute;
  top: 6.5em;
  width: 0.3em;
  height: 2.5em;
  background-color: black;
}

.character__hand {
  position: absolute;
  top: 6.8em;
  width: 0.3em;
  height: 1.7em;
  transform-origin: top;
  background-color: black;
}

.hand--left {
  transform: rotate(30deg);
}

.hand--right {
  transform: rotate(-30deg);
}

.character__leg {
  position: absolute;
  top: 9em;
  width: 0.3em;
  height: 2em;
  transform-origin: top;
  background-color: black;
}

.leg--left {
  transform: rotate(20deg);
}

.leg--right {
  transform: rotate(-20deg);
}

.question__container {
  width: 100%;
}

.question__list {
  display: flex;
  justify-content: center;
}

.question__el + .question__el {
  margin-left: 0.3em;
}

.question__letter {
  font-size: 1.3rem;
  border-bottom: 2px solid black;
}

.question {
  display: flex;
  justify-content: center;
  text-align: center;
}

.question__text {
  margin-left: 0.3rem;
  font-weight: 700;
}

.errors {
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: -0.7em;
}

.errors__text {
  margin: 0;
}

.errors__numbers {
  margin: 0 0 0 0.3rem;
  color: rgb(205, 9, 9);
}

.keyboard__list {
  display: flex;
  margin-top: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.keyboard__el {
  margin-top: 0.7rem;
}

.keyboard__el + .keyboard__el {
  margin-left: 0.3rem;
}

.keyboard__btn {
  display: block;
  padding: 0.5em;
  color: var(--block-text-color);
  background-color: var(--block-color);
}

:root {
  font-size: calc(0.5em + 1vw);
  line-height: 1.3;
  box-sizing: border-box;
  font-family: "Bitter", "Arial", serif;
  font-weight: normal;
  font-display: swap;
  height: 100%;
  background-color: var(--page-color);
  background-image: linear-gradient(90deg, var(--page-color), var(--page-color-gradient));
  color: var(--page-text-color);
  overflow-x: hidden;
}
@media (max-width: 600px) {
  :root {
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  :root {
    font-size: 20px;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  padding: 0;
  margin: 0;
}

button {
  border: none;
  cursor: pointer;
  background-color: transparent;
}

svg path {
  pointer-events: none;
}

*,
::before,
::after {
  box-sizing: inherit;
}

.double-container {
  max-width: 1440px;
  padding: 0 20px;
  margin: 1px auto;
}

/*Вы должны начинать использовать @use или @forward вместо @import ('') - потому что всеми привычный обычный импорт в версии 3.0.0 sass будет удален из функиональности. Пока что это может использоваться, но в консоли уже сейчас можно прослеживать warning сообщения от Sass об удалении импортов в новой версии препроцессора.*/